.nav {
  margin-right: 100px;
  margin-bottom: 40px;
  margin-left: 100px;
  position: relative;
}

.nav [type="checkbox"],
.nav label.nav__toggleSubMenu {
  display: none;
}

.nav label.nav__toggleMenu {
  font-size: 18px;
  text-transform: uppercase;
  color: #a59552;
  cursor: pointer;
  padding: 10px 35px;
  position: relative;
  display: none;
}

.nav label.nav__toggleMenu::after {
  content: "";
  width: 20px;
  height: 20px;
  background-image: url("../img/icon-menu.svg");
  background-size: cover;
  position: absolute;
  left: 5px;
  top: 10px;
}

.nav__list {
  list-style-type: none;
  padding-left: 0;
  display: flex;
  justify-content: space-between;
}

.nav__link {
  text-transform: uppercase;
  color: @colorText;
  font-size: 18px;
  margin: 0 10px;
  padding: 3px 5px;
  border-bottom: 1px solid transparent;
  transition: 0.6s;

  &:hover {
    color: #a59552;
    border-bottom: 1px solid @colorGreen;
  }
}

.nav__link--active {
  color: #a59552;
  border-bottom: 1px solid @colorGreen;
}

.nav__container {
  position: relative;

  &:hover > .nav__services {
    display: block;
  }
}

.nav__services {
  list-style-type: none;
  background: #ffffff;
  width: 330px;
  padding-bottom: 10px;
  padding-left: 30px;
  font-size: 16px;
  position: absolute;
  top: 24px;
  left: -25px;
  z-index: 5;
  display: none;
}

.nav__servicesLink {
  display: block;
  line-height: 24px;
  padding: 10px 0;
  color: @colorText;
  transition: 0.4s;

  &:hover {
    color: @colorGreen;
  }
}

.nav__toggleIcon {
  width: 10px;
  transform: rotate(-90deg);
  position: absolute;
  top: 12px;
  right: 20px;
}

@media screen and (max-width: 875px) {
  .nav {
    margin-right: 20px;
    margin-left: 20px;
  }
}

@media screen and (max-width: 715px) {
  .nav {
    z-index: 10;
    border-color: #ffffff;
  }

  .nav__list {
    width: 320px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    /*box-shadow: 5px 5px 5px 5px rgba(0, 0, 0, .2);*/
    display: block;
    overflow: hidden;
    max-height: 0;
    transition: max-height 0.3s;
    position: absolute;
    top: 30px;
    left: -20px;
  }

  .nav .nav__link {
    color: #a59552;
    padding: 10px 20px;
    margin: 0;
    border-bottom: none;
    display: block;

    &:active {
      background-color: @colorGreen;
      color: #ffffff;
    }
  }

  .nav__list li {
    width: 100%;
    border-bottom: 1px solid #a59552;
  }

  .nav__list li:last-child {
    border-bottom: none;
  }

  .nav label.nav__toggleMenu {
    display: block;
    transition: 0.3s;
  }

  .nav__services {
    position: relative;
    top: auto;
    left: 20px;
    padding-bottom: 0;
    padding-left: 0;
    width: auto;
    display: none;
  }

  input.nav__toggleMenu:checked + label.nav__toggleMenu {
    background-color: @colorGreen;
    color: #ffffff;
  }

  input.nav__toggleMenu:checked ~ .nav__list,
  input.nav__toggleSubMenu:checked ~ .nav__services {
    max-height: 3000px;
    transition: max-height 2s ease-in;
    display: block;
  }

  .nav label.nav__toggleSubMenu {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    display: block;
  }

  input.nav__toggleSubMenu:checked + .nav__link {
    background-color: @colorGreen;
    color: #ffffff;
  }
}
