.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-height: 180px;
  padding: 0 20px;
  position: relative;
  overflow: hidden;
}

.header__phone {
  display: flex;
  align-items: center;
}

.header__phone-link {
  color: #666666;
  font-size: 20px;
  margin-left: 10px;
}

.header__logo {
  margin: 0 20px;
}

.header__decor {
  position: absolute;
  bottom: -650px;
  right: -30px;
  width: 325px;
  height: 367px;
  background-image: url("../img/icon-img-logo.png");
  z-index: -1;
}

.header__decor--services {
  &::after {
    bottom: -510px;
    right: 0;
  }
}

.header__decor--projectWork {
  &::after {
    left: 10px;
    bottom: 490px;
  }
}

@media screen and (max-width: 730px) {
  .header {
    justify-content: center;
    padding: 20px 15px;
  }

  .header .button {
    margin-top: 10px;
  }
}

@media screen and (max-width: 535px) {
  .header__logo {
    margin: 20px;
  }

  .header .button {
    margin-top: 0;
  }
}
