.portfolioProject {
  display: flex;
  margin: 50px 0;
  padding: 0 20px;
}


.portfolioProject__text {
  width: calc(70% - 50px);
  margin-left: 50px;
  font-size: 18px;
  line-height: 28px;
}

.portfolioProject__text p {
  margin-top: 0;
}

.portfolioProject__video {
  position: relative;
  padding-bottom: 56.25%;
}

.portfolioProject__video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 900px) {
  .portfolioProject {
    flex-direction: column;
  }

  .portfolioProject__video {
    width: auto;
    max-width: 650px;
    margin: 0 auto;
  }

  .portfolioProject__text {
    width: auto;
    margin-top: 30px;
    margin-left: 0;
  }
}
