.sections {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-size: 0;
  padding: 0 20px;
  margin: 120px -10px 80px -10px;
}

.sections__item {
  position: relative;
  width: calc(33.3% - 20px);
  margin: 10px;
}

.sections__link {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  width: 100%;
  height: 100%;
  transition: 0.3s;

  &:hover {
    background: rgba(129, 216, 208, 0.85);
  }
}

.sections__link-text {
  font-size: 25px;
  font-weight: 800;
  text-transform: uppercase;
  margin-left: 10px;
}

@media screen and (max-width: 800px) {
  .sections {
    justify-content: center;
  }

  .sections__item {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 580px) {
  .sections__item {
    width: 75%;
  }
}

@media screen and (max-width: 400px) {
  .sections__item {
    width: auto;
  }
}
