.slider {
  background: @colorGreenLight;
  margin-bottom: 110px;
  font-size: 0;
  z-index: 5;
}

.slide {
  max-width: 1216px;
  margin: 0 auto;
  position: relative;
  display: flex;
  justify-content: center;
}

.slideNext {
  display: none;
}

.slide__nav {
  position: absolute;
  left: 0;
  height: 100%;
  z-index: 5;
  padding: 40px;
  cursor: pointer;
  background: transparent;
  border: none;
  transition: 0.3s;

  &:hover {
    background: rgb(129, 216, 208);
  }
}

.slide__nav img {
  width: 28px;
}

.slide__nav--forward {
  transform: scale(-1, 1);
  left: auto;
  right: 0;
}

.slider__controls {
  position: absolute;
  bottom: -40px;
  left: 50%;
  margin-left: -100px;
  display: flex;
}

.slider__controlsItem {
  width: 20px;
  height: 20px;
  border: 2px solid @colorGreen;
  border-radius: 50%;
  margin: 0 10px;
}

.slider__controlsItem--active {
  background: @colorGreen;
  transition: 0.3s;
}
@media screen and (max-width: 600px) {
  .slider {
    margin-bottom: 60px;
  }

  .slide__nav {
    padding: 20px;
  }

  .slide__nav img {
    width: 15px;
  }

  .slider__controls {
    margin-left: -50px;
    bottom: -20px;
  }

  .slider__controlsItem {
    width: 10px;
    height: 10px;
    margin: 0 5px;
  }
}
