.gallery {
  margin-bottom: 50px;
  font-size: 0;
}

.gallery__main {
  margin-bottom: 5px;
  padding-bottom: 10px;
  border-bottom: 1px solid @colorGreen;
  width: 370px;
}

.gallery__items {
  display: flex;
  flex-wrap: wrap;
  width: 370px;
  margin-right: -5px;
  margin-left: -5px;
}

.gallery__item {
  width: calc(25% - 10px);
  margin: 5px;
}

.gallery__main,
.gallery__item {
  cursor: pointer;
}

@media screen and (max-width: 900px) {
  .gallery {
    max-width: 900px;
    margin: 0 auto;
  }

  .gallery__main,
  .gallery__items {
    width: auto;
  }

  .gallery__items {
    margin-bottom: 35px;
  }

  .gallery__item {
    width: calc(20% - 10px);
  }
}

@media screen and (max-width: 500px) {
  .gallery__item {
    width: calc(25% - 10px);
  }
}
